<!-- 门诊信息管理 -->
<template>
  <div class="list-common-box">
    <!-- 标题 -->
    <div class="title-box">
       <h2>门诊信息管理</h2>
      <div class="rightbox">
        <div class="item" @click="handleReport()">
           <cc-svg-icon icon-class="merge"></cc-svg-icon>
           <span>合并报告</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">身份证号</span>
            <el-input v-model.trim="searchKey.idNo" placeholder="请输入身份证号" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">姓名</span>
            <el-input v-model.trim="searchKey.userName" placeholder="请输入姓名" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">性别</span>
            <el-select placeholder="请选择性别" v-model="searchKey.sexType" clearable>
              <el-option v-for="item in sexOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box noselection-table">
        <el-table ref="multipleTable" :data="dataList" tooltip-effect="dark" stripe height="100%" @select="handleSelectionChange"> 
          <el-table-column type="selection" width="55" :selectable="selectable"></el-table-column>
          <el-table-column prop="userName" label="姓名" align="center" width="120">
            <template slot-scope="scope">
              {{scope.row.userName?scope.row.userName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="idNo" label="身份证号" align="center">
            <template slot-scope="scope">
              {{scope.row.idNo?scope.row.idNo:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="sexTypeStr" label="性别" align="center" width="90">
            <template slot-scope="scope">
              {{scope.row.sexTypeStr?scope.row.sexTypeStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="mobile" label="电话号码" align="center" width="130">
            <template slot-scope="scope">
              {{scope.row.mobile?scope.row.mobile:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerOrganization" label="机构" align="center">
            <template slot-scope="scope">
              {{scope.row.registerOrganization?scope.row.registerOrganization:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerUser" label="接诊医生"  align="center" width="120">
            <template slot-scope="scope">
              {{scope.row.registerUser?scope.row.registerUser:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="最后测量时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="体检项目"  align="center" :show-overflow-tooltip="true">
            <template slot-scope="scope" v-if="scope.row.items.length>0">
              <span v-for="(item,index) in scope.row.items" :key="index">
                 {{item.itemName}}{{index!==scope.row.items.length-1?'，':''}}
              </span>
            </template>
            <template v-else>{{EMPTYTEXT}}</template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="100">
            <template slot-scope="scope">
              <el-button type="primary" @click="handleView(scope.row)" class="green">查看详情</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'family-archives',
    data() {
      return {
        searchKey:{
          idNo:'',//身份证号
          userName:'', //姓名
          sexType:''//性别
        },
        sexOption:[
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '男'
          },
          {
            value: 2,
            label: '女'
          }
        ],
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [], //门诊信息列表
        selectReportList:[],//要合并的报告集合
      }
    },
    created() {
       this.getDatalist()
    },
    methods: {
      //只能选择一个人的报告进行合并
      selectable(row,index){ 
        if(this.selectReportList.length>0){
          let select = this.selectReportList.find(it=>row.recordId===it.recordId) 
          if(select){
            return true
          }else{
            return false
          }
        }else{
          return true
        }
      },
      //合并报告
      handleReport(scope) {
        if(this.selectReportList.length<2){
          this.$notify({title: '提示',message:'至少选择两个报告进行合并',type: 'warning'});
        }else{
          this.$router.push({
            path: '/clinic-manage/report',
            query:{
              ids:this.selectReportList.map(item=>item.resultId).join(",")
            }
          })
        }
      },
      //查看详情
      handleView(scope) {
        this.$router.push({
          path: '/clinic-manage/detail',
          query:{
            id:scope.resultId
          }
        })
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
        this.selectReportList = []
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //表格某项选中
      handleSelectionChange(selection,row) {
        if(selection.length>4){
          this.$notify({title: '提示',message:'最多选择四个报告进行合并',type: 'warning'});
          this.$refs.multipleTable.toggleRowSelection(row,false);
        }else{
          this.selectReportList = []
          selection.forEach(item=>{
            let obj = {}
            obj.recordId = item.recordId
            obj.resultId = item.resultId
            this.selectReportList.push(obj)
          })
        }
      },
      //获取门诊信息列表
      getDatalist() {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/examResult/getDtoByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //门诊信息
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less'>
.noselection-table{
  thead {
    .el-table-column--selection {
      .cell {
        display: none;
      }
    }
  }
}
</style>